*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  white-space: normal;
  word-break: normal;
}

ul {
  list-style: none;
  list-style-type: none;
}

a {
  text-decoration: none;
  color: inherit;
}

button,
input {
  border: none;
  background: none;
  outline: none;
  font-family: inherit;
}

img,
picture,
video,
svg {
  max-width: 100%;
  display: block;
  height: auto;
}

input,
textarea,
select,
button {
  font: inherit;
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

button {
  cursor: pointer;
}

sup {
  line-height: 0;
  font-size: smaller;
  vertical-align: super;
}

sub {
  line-height: 0;
  font-size: smaller;
  vertical-align: sub;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

span {
  color: #f6d700;
}

.pulse-container {
  position: relative;
  width: 100px;
  height: 100px;
  margin: 0 auto;
  transform-style: preserve-3d;
}

.pulse-ring {
  position: absolute;
  border: 2px solid #ffd700;
  border-radius: 50%;
  transform-origin: center;
  pointer-events: none;
  visibility: visible;
  box-sizing: border-box;
  display: table;
  opacity: 0.8;
}

.pulse-ring:nth-child(1) {
  z-index: 6;
  top: 243px;
  left: calc(50% - 600px + 400px);
  width: 361px;
  height: 359px;
  animation: pulse-wave 2.5s cubic-bezier(0.4, 0, 0.2, 1) infinite;
}

.pulse-ring:nth-child(2) {
  z-index: 5;
  top: 189px;
  left: calc(50% - 600px + 310px);
  width: 528px;
  height: 533px;
  animation: pulse-wave 3s cubic-bezier(0.4, 0, 0.2, 1) infinite;
  animation-delay: 0.1s;
}

.pulse-ring:nth-child(3) {
  z-index: 4;
  top: 141px;
  left: calc(50% - 600px + 161px);
  width: 719px;
  height: 705px;
  animation: pulse-wave 3.5s cubic-bezier(0.4, 0, 0.2, 1) infinite;
  animation-delay: 0.2s;
}

.pulse-ring:nth-child(4) {
  z-index: 3;
  top: 66px;
  left: calc(50% - 600px + 82px);
  width: 880px;
  height: 889px;
  animation: pulse-wave 4s cubic-bezier(0.4, 0, 0.2, 1) infinite;
  animation-delay: 0.3s;
}

.pulse-ring:nth-child(5) {
  z-index: 2;
  top: 3px;
  left: calc(50% - 600px - 4px);
  width: 1020px;
  height: 1060px;
  animation: pulse-wave 4.5s cubic-bezier(0.4, 0, 0.2, 1) infinite;
  animation-delay: 0.4s;
}

.pulse-ring-logo {
  content: url("../images/noroot.png.webp");
  width: 167px;
  height: 190px;
  z-index: 21;
  top: 318px;
  left: calc(50% - 600px + 499px);
  visibility: visible;
  position: absolute;
  box-sizing: border-box;
  display: block;
  opacity: 0.8;
  animation: pulse-img 3s ease-in-out infinite;
  transform-origin: center;
}

@keyframes pulse-img {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes pulse-wave {
  0% {
    transform: scale(1);
    border-width: 2px;
    opacity: 0.8;
  }
  20% {
    border-width: 4px;
    border-color: rgba(255, 215, 0, 0.9);
    opacity: 0.7;
  }
  40% {
    transform: scale(1.05);
    border-width: 3px;
    opacity: 0.6;
  }
  60% {
    border-width: 4px;
    border-color: rgba(255, 215, 0, 0.9);
    opacity: 0.7;
  }
  80% {
    transform: scale(1.03);
    border-width: 2px;
    opacity: 0.75;
  }
  100% {
    transform: scale(1);
    border-width: 2px;
    opacity: 0.8;
  }
}
.page {
  font-family: "Gilroy", Arial, sans-serif;
  max-width: 100vw;
  width: 100%;
  margin: 0 auto;
  font-size: 20px;
  color: #26262b;
  display: flex;
  flex-direction: column;
}

.header {
  max-width: 1200px;
  width: 80%;
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
  row-gap: 30px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: #ebebeb;
  margin: 0 auto;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.2);
  font-size: 1em;
  white-space: nowrap;
  word-break: normal;
  border-style: solid;
  color: #4b4b4f;
  font-family: "Gilroy", Arial, sans-serif;
  line-height: 1.35;
  font-weight: 600;
  text-transform: uppercase;
  opacity: 0.98;
  border-color: transparent;
  border-style: solid;
  will-change: transform;
  backface-visibility: hidden;
  transform: translateZ(0);
  backface-visibility: hidden;
}
.header__logo-cont {
  display: flex;
  margin-left: 10px;
  align-items: center;
  gap: 10px;
  font-size: inherit;
  opacity: 0.9;
  font-size: 1.1em;
  font-weight: 500;
}
.header__btn {
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 30px;
  color: #26262b;
  font-size: 1em;
  font-family: "Gilroy", Arial, sans-serif;
  line-height: 1.55;
  font-weight: 600;
  border-radius: 15px;
  background-color: #f6d700;
  background-position: center center;
  border-color: transparent;
  border-style: solid;
  max-height: 35px;
}
.header__burger-btn {
  margin-left: 40px;
}

@media screen and (max-width: 1420px) {
  .header {
    font-size: 1em;
  }
}
.contacts {
  display: flex;
  opacity: 0.9;
  justify-content: start;
  gap: 30px;
  margin-left: 40px;
  font-weight: 500;
  font-size: 1em;
}
.contacts__item:nth-child(2) .contacts__link {
  font-size: 1.2em;
  text-transform: lowercase;
}
.contacts__item {
  display: flex;
  align-items: center;
}
.contacts__img-link svg {
  border-radius: 15px;
}
.contacts__link {
  margin-left: 15px;
}

.navigation {
  width: 100%;
  height: auto;
  display: none;
}
.navigation__active {
  display: block;
}
.navigation__list {
  display: flex;
  padding-bottom: 45px;
  font-size: 20px;
  font-weight: 500;
  text-transform: none;
  column-gap: 80px;
  justify-content: center;
}
.navigation__item {
  display: flex;
  width: 10%;
  flex-wrap: wrap;
}

